import React from 'react';
import { Grid, Typography } from '@material-ui/core';
import * as selectors from '../../redux/selectors';
import { useSelector } from 'react-redux';

function FormSubmit() {
  const percentage = useSelector(selectors.getPercentage);
  const months = useSelector(selectors.getMonths);
  const firstInstallment = useSelector(selectors.getFirstinstallment);
  const priceForEveryMonth = useSelector(selectors.getPriceForEveryMonths);
  const information = useSelector(selectors.getInformation);
  const trusted = useSelector(selectors.getTrusted);

  const handleSubmit = () => {
    console.log(
      `  ${percentage}%  ,    17%  ${months} ,      ${firstInstallment}     ${priceForEveryMonth} .
      
       : ${information.name}.
       : ${information.phone}.
       : ${information.workPhone}.
       : ${information.location}.
      : ${trusted.name}.
       : ${trusted.phone}.
       : ${trusted.person} 
      `
    );
    alert(
      `  ${percentage}%  ,    17%  ${months} ,      ${firstInstallment}     ${priceForEveryMonth} .
      
       : ${information.name}.
       : ${information.phone}.
       : ${information.workPhone}.
       : ${information.location}.
      : ${trusted.name}.
       : ${trusted.phone}.
       : ${trusted.person} 
      `
    );
  };
  return (
    <Grid
      container
      className="form_submit"
      direction="row"
      alignItems="center"
      spacing={4}
    >
      <Grid item xs={12} sm={6}>
        <Typography>
                {' '}
          <a href="/autocredit" style={{ color: '#07b1fd' }}>
             
          </a>
        </Typography>
      </Grid>
      <Grid
        item
        xs={12}
        sm={6}
        container
        direction="row"
        justifyContent="flex-end"
      >
        <button className="submit_button" onClick={handleSubmit}>
           
        </button>
      </Grid>
    </Grid>
  );
}

export default FormSubmit;
